import React, { useEffect, useState } from 'react';
import { ImageModalProps } from './types';
import { Checkbox, Image, Input, Modal } from 'antd';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectImagesList, updateImagesList } from '../../features/gallery/gallerySlice';
import './ImageModal.css';

export const ImageModal: React.FC<ImageModalProps> = ({ description, id, isFavorite, isModalOpen, setIsModalOpen, url }) => {
  const { TextArea } = Input;
  const [modalDescription, setModalDescription] = useState('');
  const [modalIsFavorite, setModalIsFavorite] = useState(false);

  useEffect(() => {
    setModalDescription(description);
  }, [description]);

  useEffect(() => {
    setModalIsFavorite(isFavorite);
  }, [isFavorite])

  const imagesList = useAppSelector(selectImagesList);
  const dispatch = useAppDispatch();

  const applyModalChanges = () => {
    const newList = imagesList.map((image) => {
      if (image.id === id) {
        return {...image, description: modalDescription, isFavorite: modalIsFavorite};
      } else {
        return image;
      }
    })
    dispatch(updateImagesList(newList));
  }

  const handleOk = () => {
    applyModalChanges();
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setModalDescription(description);
    setModalIsFavorite(false);
    setIsModalOpen(false);
  };

  return (
    <Modal
      okText="Save"
      onCancel={handleCancel}
      onOk={handleOk}
      open={isModalOpen}
      title="Edit Image"
    >
      <Image
        alt={description}
        fallback="data:image/png;base64,"
        height={314}
        preview={false}
        rootClassName="edit-modal-image"
        src={url}
        width={471}
      />
      <TextArea
        allowClear={true}
        value={modalDescription}
        maxLength={100}
        onChange={(event) => setModalDescription(event.target.value)}
        placeholder="Enter a description"
        rows={4}
        showCount
        style={{ height: 120, marginBottom: 24, marginTop: 24 }}
      />
      <Checkbox onChange={(event) => setModalIsFavorite(event.target.checked)} checked={modalIsFavorite}>Set as Favorite</Checkbox>
    </Modal>
  )
}