import React, { useState } from 'react';
import { ImageCardProps } from './types';
import { Divider, Image, Tooltip } from 'antd';
import {
  DeleteOutlined,
  EditOutlined,
  InfoCircleFilled,
  StarOutlined,
} from '@ant-design/icons';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectImagesList, updateImagesList } from '../../features/gallery/gallerySlice';
import { ImageModal } from '../ImageModal/ImageModal';
import './ImageCard.css';

export const ImageCard: React.FC<ImageCardProps> = ({ description, id, isFavorite, url }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const imagesList = useAppSelector(selectImagesList);
  const dispatch = useAppDispatch();

  const deleteImage = () => {
    const newList = imagesList.filter(image => image.id !== id);
    dispatch(updateImagesList(newList));
  }

  const editImage = () => {
    setIsModalOpen(true);
  };

  return (
    <div className='card-container'>
      <div className='card-image-wrapper'>
        <Image
          alt={description}
          fallback="data:image/png;base64,"
          height={160}
          src={url}
          width={240}
        />
        <div className='card-symbol'>
          {isFavorite && (
            <div className='card-symbol-bg'>
              <StarOutlined />
            </div>
          )}
        </div>
        <div className='card-symbol card-symbol-left'>
          {description && (
            <Tooltip title={description}>
              <div className='card-symbol-bg'>
                <InfoCircleFilled />
              </div>
            </Tooltip>
          )}
        </div>
      </div>

      <div className='card-actions'>
        <button className='card-button' onClick={editImage}>
          <EditOutlined className='card-icon' />
        </button>
        <ImageModal
          description={description}
          id={id}
          isFavorite={isFavorite}
          isModalOpen={isModalOpen}
          setIsModalOpen={setIsModalOpen}
          url={url}
          />

        <Divider type="vertical" />

        <button className='card-button' onClick={deleteImage}>
          <DeleteOutlined className='card-icon' />
        </button>
      </div>
    </div>
  )
}